import { Component, OnInit } from '@angular/core';
import { Transferencia } from '../shared/model/transferencia.model';
import { CuentasService } from '../shared/services/cuentas.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-transferencia',
  templateUrl: './transferencia.component.html',
  styleUrls: ['./transferencia.component.scss']
})
export class TransferenciaComponent implements OnInit {
  transferencia : Transferencia = new Transferencia();
  cuentasUsuario : any[];
  constructor(private cuentas : CuentasService, private route : Router) {
    this.cuentas.getCuentas(JSON.parse(localStorage.getItem("user")).id)
      .subscribe(data => this.cuentasUsuario = data, error=> this.route.navigate(['404']));
  }

  ngOnInit() {
  }

}



