import { Component, OnInit, Inject} from '@angular/core';
import { Usuario } from '../shared/model/usuario.model';
import { UserService } from '../shared/services/user.service';
import { MatDialog } from '@angular/material';
import { AlertComponent } from '../alert/alert.component';
import { Router } from '@angular/router';
import { Login } from '../shared/model/login.model';

@Component({
  selector: 'app-registro',
  templateUrl: './registro.component.html',
  styleUrls: ['./registro.component.scss'],
  providers: [UserService]
})
export class RegistroComponent implements OnInit {
  constructor(private usuarioService : UserService, 
              public dialog : MatDialog,
              private route : Router) { }

  usuario : Usuario = new Usuario();
  title : String;
  body : String;

  registrar(){
    this.usuarioService.registrar(this.usuario)
      .subscribe(
        ok =>{
          this.title = "Enhorabuena";
          this.body = "Tu registro ha sido realizado con éxito.";
          this.openDialog(true);
        }, 
        error =>{
          this.title = "Error";
          this.body = "El registro no ha podido realizarse con éxito."
          this.openDialog(false);
        }
      )
  }

  openDialog(ok : Boolean): void {
    let dialogRef = this.dialog.open(AlertComponent, {
      width: '400px',
      data: { title: this.title, body: this.body}
    });

    if(ok){
      dialogRef.afterClosed().subscribe(result => {
        var login = new Login();
        login.dni = this.usuario.dni;
        login.pin = this.usuario.pin;
        this.usuarioService.login(login);
      });
    }
  }

  ngOnInit() {
  }

}



